import {Scene, Engine ,ShaderMaterial, ArcRotateCamera, Color4, Vector3, Camera, Effect, MeshBuilder, SceneLoader ,  CubeTexture, HemisphericLight, Vector2, Color3} from '@babylonjs/core';
import { EffectScope } from 'vue';

export class HomePage{

    scene : Scene;
    engine : Engine;

    constructor(private canvas:HTMLCanvasElement){
        this.engine = new Engine(this.canvas, true, {preserveDrawingBuffer: true,stencil:true});

        //this.engine.displayLoadingUI();

        this.scene = this.CreateScene();
        
        Effect.ShadersStore["basicVertexShader"] = `
        attribute vec3 position;
        attribute vec3 normal;
        attribute vec2 uv;
        
        uniform mat4 worldViewProjection;
        uniform float time;
        
        varying vec3 vPosition;
        varying vec2 vUV;
        
        void main() {
            vec4 p = vec4( position, 1. );
            vPosition = p.xyz;
            vUV = uv;	
            gl_Position = worldViewProjection * p;
        }
        `;

        Effect.ShadersStore["basicFragmentShader"] = `uniform float iTime;
        uniform vec2 iResolution;
        
        varying vec3 vPosition;
        varying vec2 vUV;
     
        void main() 
        {
            vec2 uv = (vUV * iResolution.xy-.5*iResolution.xy)/iResolution.y;
        
            float a = sin(atan(uv.y,uv.x));
            float am = abs(a-.5)/4.;
            float l = length(uv);


           float s = smoothstep(.4,.2*sin(iTime)+0.35,l);
            vec3 col = vec3(0.);

           float s1 = smoothstep(.4,.2*sin(iTime)+0.1,l);
            col += s;
            col += s1;
            gl_FragColor = vec4(col,1.0);
        }
        `;

        this.engine.runRenderLoop(() => {
            this.scene.render();
        });

        window.addEventListener('resize', () => {
            this.engine.resize();
        })
    }

    CreateScene(): Scene {
        const scene = new Scene(this.engine);
        scene.clearColor = new Color4(.23,.24,.25,1.0);
        const camera = new ArcRotateCamera("rotateCamera",-Math.PI/2,Math.PI,3,new Vector3(0,0,0), this.scene);
        camera.setTarget(Vector3.Zero());
        camera.wheelPrecision = 40;
        // camera.mode = Camera.ORTHOGRAPHIC_CAMERA;
        camera.attachControl();
        // camera.orthoTop = 1;
        // camera.orthoBottom = -1;
        // camera.orthoLeft = -1;
        // camera.orthoRight = 1;

        const ground = MeshBuilder.CreateGround("ground", {width : 2, height: 2}, this.scene);
        ground.position = new Vector3(0,0,0);
      
        let time = 0;
        console.log(scene);
        scene.registerBeforeRender(() => {
            time += 0.01;
            shaderMaterial.setFloat("iTime", time);
            const aRation = scene.getEngine().getAspectRatio(camera);
            shaderMaterial.setVector2("iResolution",new Vector2(aRation,1));
        })
        const shaderMaterial = new ShaderMaterial("shader", scene, {
        vertex : "basic",
        fragment : "basic"
        },
        {
            attributes: ["position", "normal", "uv"],
            uniforms: ["world", "worldView", "worldViewProjection",  "view", "iTime","projection"]
        });
        shaderMaterial.backFaceCulling = false;
        ground.material = shaderMaterial;

        return scene;
    }
}